<script>
  export default {
    data() {
      return {
        formData: { name: "", mail: "", select: "", date: "" },
        formData1: {
          a: [
            {
              label: "1",
              value: ""
            }
          ]
        },
        options: [
          {
            value: "1",
            label: ""
          },
          {
            value: "2",
            label: ""
          },
          {
            value: "3",
            label: ""
          },
          {
            value: "4",
            label: ""
          },
          {
            value: "5",
            label: ""
          }
        ],
        rules: {
          name: [{ required: true, message: "", trigger: "blur" }],
          mail: [
            { required: true, message: "", trigger: "blur" },
            { type: "email", message: "", trigger: "blur" }
          ],
          select: [{ required: true, message: "", trigger: "change" }],
          date: [{ required: true, message: "", trigger: "change" }]
        }
      };
    },
    methods: {
        handleSubmit() {
            console.log(this.$refs.form);
            this.$refs.form.validate((valid) => {
                if (valid)  console.log('');
                else console.log('');
            })
        },
        handleSubmit1() {
          this.$refs.form1.validate(valid => {
            if (valid) console.log("");
            else console.log("");
          });
        },
        add() {
          this.formData1.a.push({
            label: "1",
            value: ""
          });
        },
        handleReset() { this.$refs.form.resetFields() }
    }
  };
</script>


